import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import {HttpClientModule} from '@angular/common/http';
import {DataService} from './data.service';
import { ViewTopComponent } from './view-top/view-top.component';
import { ViewRightComponent } from './view-right/view-right.component';
import { ViewBottomComponent } from './view-bottom/view-bottom.component';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import {ModalModule, TooltipModule} from 'ngx-bootstrap';
import {AppRoutingModule, routingComponents} from './app-routing.module';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { AnimalListComponent } from './animal-list/animal-list.component';
import {ViewLeftComponent} from './view-left/view-left.component';
import {ViewCenterComponent} from './view-center/view-center.component';
import {AngularFontAwesomeModule} from 'angular-font-awesome';
import { CartSectionComponent } from './cart-section/cart-section.component';
import {NgbCarouselModule} from '@ng-bootstrap/ng-bootstrap';
import { WelcomeComponent } from './welcome/welcome.component';
import { DetailComponent } from './detail/detail.component';

@NgModule({
  declarations: [
    AppComponent,
    ViewTopComponent,
    ViewRightComponent,
    ViewLeftComponent,
    ViewCenterComponent,
    ViewBottomComponent,
    routingComponents,
    PageNotFoundComponent,
    AnimalListComponent,
    CartSectionComponent,
    WelcomeComponent,
    DetailComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    BsDropdownModule.forRoot(),
    TooltipModule.forRoot(),
    ModalModule.forRoot(),
    AppRoutingModule,
    AngularFontAwesomeModule,
    NgbCarouselModule
  ],
  providers: [DataService, {provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }



