import {Input, NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ViewCenterComponent} from './view-center/view-center.component';
import {ViewLeftComponent} from './view-left/view-left.component';
import {PageNotFoundComponent} from './page-not-found/page-not-found.component';
import {AnimalListComponent} from './animal-list/animal-list.component';
import {WelcomeComponent} from './welcome/welcome.component';
import {DetailComponent} from './detail/detail.component';

const routes: Routes = [
  {path: '', component: AnimalListComponent},
  {path: 'details', component: DetailComponent},
  {path: 'animalList/:currentType', component: AnimalListComponent},
  {path: '**', component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
export const routingComponents = [WelcomeComponent, AnimalListComponent, PageNotFoundComponent, DetailComponent]



