import { Component, OnInit } from '@angular/core';
import {Animal} from '../animal';
import {DataService} from '../data.service';
import {ActivatedRoute, NavigationEnd, Router} from '@angular/router';

@Component({
  selector: 'app-animal-list',
  templateUrl: './animal-list.component.html',
  styleUrls: ['./animal-list.component.css']
})

export class AnimalListComponent implements OnInit {

  public currentSort: String = 'Default Sort';
  public currentType: String;
  public animals: Animal[] = [];
  constructor(private dataService: DataService, private router: Router, private route: ActivatedRoute) {
    // override the route reuse strategy
    this.router.routeReuseStrategy.shouldReuseRoute = function() {
      return false;
    }

    this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationEnd) {
        // trick the Router into believing it's last link wasn't previously loaded
        this.router.navigated = false;
        // if you need to scroll back to top, here is the right place
        window.scrollTo(0, 0);
      }
    });

  }
  ngOnInit() {
    console.log(this.animals);
    this.dataService.getAnimals().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);

      this.currentType = this.route.snapshot.paramMap.get('currentType');
    });
  }
  sortPriceAscending() {
    this.dataService.getAnimalsSortedPriceAscending().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
      this.currentSort = 'Price: Ascending';
    });
  }

  sortPriceDescending() {
    this.dataService.getAnimalsSortedPriceDescending().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
      this.currentSort = 'Price: Descending';
    });
  }

  sortAlphabetically() {
    this.dataService.getAnimalsAlphabetically().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
      this.currentSort = 'Type: A-Z';
    });
  }

  sortReverseAlphabetically() {
    this.dataService.getAnimalsReverseAlphabetically().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
      this.currentSort = 'Type: Z-A';
    });
  }
}



