import { Component, OnInit } from '@angular/core';
import {DataService} from '../data.service';

@Component({
  selector: 'app-view-left',
  templateUrl: './view-left.component.html',
  styleUrls: ['./view-left.component.css']
})
export class ViewLeftComponent implements OnInit {

  public animalsTypes: String[] = [];
  public currentType: String = 'All';
  constructor(private dataService: DataService) {}

  ngOnInit() {
    this.dataService.getAnimalsTypes().subscribe(res => {
      console.log(res);
      this.animalsTypes = res.sort((first, second) => {
        if (first > second) return 1;
        if (first < second) return -1;
        return 0;
      });
    });
  }
}



